import { useState, useEffect } from 'react';
import { useSession, useSupabaseClient } from '@supabase/auth-helpers-react';
import { Navigate, useNavigate } from 'react-router-dom';
import { Database } from './lib/supabase-types';
import LoadingIndicator from './components/shared/LoadingIndicator';
import ConnectionError from './components/shared/ConnectionError';
import { useSupabaseConnection } from './hooks/useSupabaseConnection';
import Dashboard from './components/dashboard/Dashboard';
import AdminLayout from './components/admin/AdminLayout';

export default function App() {
  const session = useSession();
  const supabase = useSupabaseClient<Database>();
  const navigate = useNavigate();
  const { isConnected, checkConnection } = useSupabaseConnection();
  const [isAdmin, setIsAdmin] = useState<boolean | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    if (!session) {
      navigate('/signin', { replace: true });
      return;
    }

    // Check if user has a profile
    const checkProfile = async () => {
      try {
        const { error: profileError } = await supabase
          .from('profiles')
          .select('id')
          .eq('id', session.user.id)
          .single();

        if (profileError && profileError.code === 'PGRST116') {
          // No profile found, redirect to complete profile
          navigate('/complete-profile', { replace: true });
          return;
        }

        if (profileError) throw profileError;

        // If we have a profile, check admin status
        const { data: isAdmin, error: adminError } = await supabase.rpc('is_admin');
        
        if (adminError) {
          if (!adminError.message.includes('Failed to fetch')) {
            throw adminError;
          }
          return;
        }
        
        setIsAdmin(isAdmin);
        setError(null);
      } catch (error) {
        console.error('Error checking profile/admin status:', error);
        setError('Failed to load user data. Please try refreshing the page.');
        setIsAdmin(false);
      } finally {
        setIsLoading(false);
      }
    };

    checkProfile();
  }, [session, supabase, navigate]);

  if (!session) {
    return <Navigate to="/signin" replace />;
  }

  if (!isConnected) {
    return <ConnectionError onRetry={checkConnection} />;
  }

  if (isLoading) {
    return <LoadingIndicator message="Loading..." />;
  }

  if (error) {
    return <LoadingIndicator message={error} />;
  }

  if (isAdmin === null) {
    return <LoadingIndicator message="Checking permissions..." />;
  }

  return isAdmin ? <AdminLayout /> : <Dashboard />;
}