import { useEffect, useState } from 'react';
import { useSearchParams, useNavigate } from 'react-router-dom';
import { useSupabaseClient } from '@supabase/auth-helpers-react';
import { Database } from '../../lib/supabase-types';
import LoadingIndicator from '../shared/LoadingIndicator';

export default function VerifyEmail() {
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const supabase = useSupabaseClient<Database>();
  const [error, setError] = useState<string | null>(null);
  const [isVerifying, setIsVerifying] = useState(true);

  useEffect(() => {
    const verifyEmail = async () => {
      try {
        // Get token and type from URL
        const token = searchParams.get('token');
        const type = searchParams.get('type');

        if (!token) {
          setError('Invalid verification link. Please check your email and try again.');
          return;
        }

        // Verify the token
        const { error: verifyError } = await supabase.auth.verifyOtp({
          token_hash: token,
          type: type as any || 'signup'
        });

        if (verifyError) {
          console.error('Verification Error:', verifyError);
          if (verifyError.message.includes('expired')) {
            throw new Error('The verification link has expired. Please sign up again.');
          } else if (verifyError.message.includes('invalid')) {
            throw new Error('Invalid verification link. Please check your email and try again.');
          }
          throw verifyError;
        }

        // Get the current session
        const { data: { session }, error: sessionError } = await supabase.auth.getSession();
        
        if (sessionError) {
          console.error('Session Error:', sessionError);
          throw sessionError;
        }

        if (!session?.user) {
          throw new Error('No user session found after verification');
        }

        // Update profile verified status
        const { error: updateError } = await supabase
          .from('profiles')
          .update({ 
            verified: true,
            verified_at: new Date().toISOString()
          })
          .eq('id', session.user.id);

        if (updateError) {
          console.error('Profile Update Error:', updateError);
          // Don't throw here - the verification was successful
        }

        // Sign out after verification
        await supabase.auth.signOut();

        // Redirect to sign in page after short delay
        setTimeout(() => {
          navigate('/signin', { 
            state: { 
              message: 'Email verified successfully! You can now sign in.' 
            }
          });
        }, 3000);
      } catch (err) {
        console.error('Verification Error:', err);
        setError(err instanceof Error ? err.message : 'Failed to verify email. Please try again later.');
      } finally {
        setIsVerifying(false);
      }
    };

    verifyEmail();
  }, [searchParams, supabase, navigate]);

  if (isVerifying) {
    return <LoadingIndicator message="Verifying your email..." />;
  }

  if (error) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50 dark:bg-gray-900">
        <div className="max-w-md w-full space-y-8 p-4">
          <div className="text-center">
            <h2 className="text-3xl font-extrabold text-gray-900 dark:text-white">
              Verification Failed
            </h2>
            <p className="mt-2 text-sm text-red-600 dark:text-red-400">
              {error}
            </p>
            <button
              onClick={() => navigate('/signup')}
              className="mt-4 inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700"
            >
              Return to Sign Up
            </button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-50 dark:bg-gray-900">
      <div className="max-w-md w-full space-y-8 p-4">
        <div className="text-center">
          <h2 className="text-3xl font-extrabold text-gray-900 dark:text-white">
            Email Verified!
          </h2>
          <p className="mt-2 text-sm text-gray-600 dark:text-gray-400">
            Your email has been successfully verified. You will be redirected to sign in...
          </p>
        </div>
      </div>
    </div>
  );
}